.graph-modal {
  background-color: rgba(0, 0, 0, 0.4);
}

.catalog-step {
  max-width: 460px;
  width: 100%;
  padding: 50px 32px 54px;
  background: #FFFFFF;
  border: 2px solid #243469;
  box-shadow: 4px 4px 40px rgba(36, 52, 105, 0.05);
  border-radius: 20px 0px 0px 20px;
}

.catalog-modal {
  max-width: 855px;
  width: 100%;
  padding: 44px 38px 57px;
  background: #FFFFFF;
  border: 2px solid #243469;
  box-shadow: 4px 4px 40px rgba(36, 52, 105, 0.05);
  border-radius: 20px 0px 0px 20px;

  &__close {
    background-image: url(../img/close.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    width: 41px;
    height: 41px;
    top: 34px;
    right: 24px;
  }

  &__count {
    display: flex;
    align-items: flex-end;
  }

  &__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
    color: var(--title-color);
    margin: 0;
    margin-bottom: 23px;
  }

  &__title-form {
    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 40px;

  }

  &__descr {
    display: flex;
    margin-bottom: 58px;
  }

  &__spec {}

  &__spec-title {
    ont-weight: 500;
    font-size: 18px;
    line-height: 30px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 25px;
  }

  &__list {
    max-width: 390px;
    width: 100%;
    margin-bottom: 35px;
  }

  &__item {
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: var(--primary-color);
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &__item--size {
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    text-align: right;
    color: var(--primary-color);
    flex: 1 1 auto;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__summ {
    font-weight: 500;
    font-size: 32px;
    line-height: 37px;
    color: var(--primary-color);
  }

  &__price-btn {
    position: relative;
    background-image: linear-gradient(118.92deg, #F03A2B -8.89%, #FCC009 189.86%);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.07);
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    padding: 20px 74px 20px 30px;
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    color: var(--light-color);

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: 30px;
      transform: translateY(-50%);
      width: 25px;
      height: 25px;
      background-image: url(../img/plus.svg);
      background-size: cover;
      background-position: 0 0;
      background-repeat: no-repeat;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: var(--primary-color);
  }

  &__designed {
    margin-bottom: 77px;
  }

  &__designed-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 48px;
  }

  &__designed-item {
    grid-column: 1 span;
    display: flex;
    align-items: center;
  }

  &__designed-text {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.2;
    color: var(--primary-color);
    margin-left: 46px;
  }

  &__btn {
    width: 100%;
    padding: 14px;
    position: relative;
    background: #fff;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    border: 4px solid transparent;
    background: linear-gradient(white, white) padding-box,
      linear-gradient(180deg, #F03A2B -8.89%, #FCC009 189.86%) border-box;

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: 24%;
      transform: translateY(-45%);
      width: 61px;
      height: 70px;
      background-image: url(../img/download-btn.svg);
      background-size: cover;
      background-position: 0 0;
      background-repeat: no-repeat;
    }
  }

  &__btn-text {
    font-weight: 500;
    font-size: 18px;
    line-height: 34px;
    background: linear-gradient(118.92deg, #F03A2B -8.89%, #FCC009 189.86%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 4px 4px 20px rgba(36, 52, 105, 0.03);
  }

  &__form-text {
    font-weight: 400;
    font-size: 10px;
    line-height: 16px;
    text-align: center;
    color: #C4C4C4;
    margin: 0;
    margin-bottom: 20px;
  }

  &__form-label {}

  &__form-input {
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    border: 2px solid #243469;
    padding: 10px 25px;
    width: 100%;
    margin-bottom: 20px;

    &::placeholder {
      font-weight: 700;
      font-size: 18px;
      line-height: 34px;
      color: #C4C4C4;
      opacity: 0.4;
    }

    &--textarea {
      resize: vertical;
      min-height: 50px;
    }
  }
}
