.catalog {
  padding-bottom: 90px;

  @include mobile {
    padding-bottom: 60px;
  }

  &__container {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      bottom: -2%;
      right: -15%;
      max-width: 482px;
      max-height: 482px;
      width: 100%;
      height: 100%;
      background-color: var(--title-color);
      filter: blur(400px);

      @include mobile {
        display: none;
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: -2%;
      left: -15%;
      max-width: 482px;
      max-height: 482px;
      width: 100%;
      height: 100%;
      background-color: var(--title-color);
      filter: blur(400px);

      @include mobile {
        display: none;
      }
    }
  }

  &__title {
    font-weight: 700;
    font-size: 42px;
    line-height: 48px;
    text-align: center;
    color: var(--primary-color);
    text-align: center;
    margin: 0;
    margin-bottom: 34px;

    @include tablet {
      font-size: 32px;
      line-height: 37px;
      margin-bottom: 27px;
    }
  }
}

.catalog-card {

  &__title {
    font-weight: 700;
    font-size: 28px;
    line-height: 32px;
    text-align: center;
    color: var(--title-color);
    margin: 0;
    margin-bottom: 51px;
    position: relative;

    @include tablet {
      font-size: 22px;
      line-height: 25px;
    }

    @include mobile {
      margin-bottom: 35px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 5px;
      background-image: linear-gradient(90deg, #2D3A94, #51ABE3);
      z-index: -1;

      @include mobile {
        display: none;
      }
    }
  }

  &__title-decore {
    background-color: #fff;
    padding: 0 58px;

    @include mobile {
      padding: 0;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 25px 36px;

    @include desktop {
      gap: 25px 20px;
    }
  }

  &__item {
    grid-column: 4 span;
    background: var(--light-color);
    border: 2px solid #243469;
    box-shadow: 4px 4px 40px rgba(36, 52, 105, 0.05);
    border-radius: 20px;
    padding: 40px 45px 65px;

    @include big-desktop {
      padding: 40px 28px 40px;
    }

    @include desktop {
      grid-column: 6 span;
      padding: 27px 23px 23px;
      text-align: center;
    }

    @include small-tablet {
      grid-column: 12 span;
      padding: 27px 10px 23px;
    }

    @include mobile {
      text-align: start;
    }
  }

  &__item-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
    color: var(--title-color);
    margin: 0;
    margin-bottom: 40px;

    @include tablet {
      font-size: 20px;
      line-height: 32px;
    }

    @include mobile {
      margin-bottom: 18px;
    }
  }

  &__item-image {
    margin-bottom: 15px;

    @include tablet {
      margin-bottom: 10px;
    }
  }

  &__item-descr {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include big-desktop {
      flex-direction: column;
      align-items: flex-start;
    }

    @include small-tablet {
      align-items: center;
    }
  }

  &__item-sum {
    font-weight: 500;
    font-size: 32px;
    line-height: 37px;
    color: var(--primary-color);
  }

  &__item-btns {
    @include big-desktop {
      margin-top: 25px;
    }

    @include mobile {
      margin-top: 28px;
    }
  }

  &__item-info {
    width: 87px;
    height: 60px;
    position: relative;
    background: #fff;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    border: 4px solid transparent;
    background: linear-gradient(white, white) padding-box,
      linear-gradient(118.92deg, #F03A2B -8.89%, #FCC009 189.86%) border-box;

    @include big-desktop {
      width: 145px;
    }

    @include tablet {
      width: 140px;
    }

    @include mobile {
      width: 125px;
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 14px;
      height: 38px;
      background-image: url(../img/info.svg);
      background-size: cover;
      background-position: 0 0;
      background-repeat: no-repeat;
    }
  }

  &__item-plus {
    width: 87px;
    height: 60px;
    position: relative;
    background-image: linear-gradient(118.92deg, #F03A2B -8.89%, #FCC009 189.86%);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.07);
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    margin-left: 10px;

    @include big-desktop {
      width: 145px;
    }

    @include mobile {
      width: 125px;
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 40px;
      height: 40px;
      background-image: url(../img/plus.svg);
      background-size: cover;
      background-position: 0 0;
      background-repeat: no-repeat;
    }
  }

  &__btns {
    grid-column: 12 span;
    margin-bottom: 55px;
    text-align: center;

    @include tablet {
      margin-bottom: 42px;
    }
  }

  &__btn {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: var(--title-color);

    @include mobile {
      font-size: 18px;
      line-height: 28px;
    }
  }

  &__save {
    grid-column: 12 span;
    font-weight: 700;
    font-size: 28px;
    line-height: 32px;
    text-align: center;
    color: var(--light-color);
    display: block;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    background-color: var(--title-color);
    padding: 20px 0;
    position: relative;

    @include tablet {
      font-size: 22px;
      line-height: 25px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      background: #F03A2B;
      filter: blur(300px);
    }
  }
}

.image-up {
  transform: rotate(180deg);
}

.image-down {
  transform: rotate(0);
}
