.cert {
  padding-bottom: 143px;

  @include tablet {
    padding-bottom: 108px;
  }

  @include big-mobile {
    padding-bottom: 165px;
  }

  &__container {
    position: relative;
    max-width: 1555px;

    @include big-desktop {
      max-width: 1300px;
    }

    &::before {
      content: '';
      position: absolute;
      top: -2%;
      left: -35%;
      max-width: 482px;
      max-height: 482px;
      width: 100%;
      height: 100%;
      background-color: var(--title-color);
      filter: blur(400px);
    }
  }

  &__slider {
    padding-top: 60px;
    padding-right: 50px;
    margin-left: 23px;

    @include big-desktop {
      margin-left: 57px;
      padding-right: 25px;
    }

    @include tablet {
      margin-left: -4px;
    }

    @include big-mobile {
      padding-right: 20px;
    }

  }

  &__slider-btn {
    @include bg-cover;

    width: 87px;
    height: 60px;
    position: absolute;
    top: 53.5%;
    z-index: 10;

    @include mobile {
      top: 104%;
    }

    &--prev {
      background-image: url(../img/arr-left.svg);
      left: -10px;

      @include big-desktop {
        left: 0;
      }

      @include mobile {
        left: 32px;
      }
    }

    &--next {
      background-image: url(../img/arr-right.svg);
      right: -23px;

      @include big-desktop {
        right: 0;
      }

      @include mobile {
        right: 32px;
      }
    }
  }

  &__slide {
    position: relative;
    padding-top: 70px;

    &::before {
      content: "";
      position: absolute;
      top: 30px;
      left: 39px;
      max-width: 333px;
      max-height: 452px;
      width: 104%;
      height: 100%;
      background-image: url(../img/line-top-small.svg);
      background-size: cover;
      background-position: 0 0;
      background-repeat: no-repeat;
      z-index: -1;

      @include tablet {
        left: 32px;
      }

      @include big-mobile {
        left: 15px;
        width: 102%;
        height: 85%;
      }
    }
  }
}

.cert-image {

  @include big-desktop {
    width: 330px;
  }

  @include tablet {
    width: 315px;
  }
}
