.burger {
  @include burger;
  display: none;

  @include desktop {
    display: block;
    margin-left: 11%;
    margin-top: 12px;
    z-index: 100;
  }

  @include big-mobile {
    right: 10px;

    & .burger--active {
      right: 40px;
    }
  }
}

.nav {

  @include desktop {
    position: fixed;
    right: 0;
    top: 0;
    overflow-y: auto;
    padding: 80px 45px 45px;
    height: 100vh;
    background-color: var(--title-color);
    box-shadow: 2px 0px 8px #494949;
    transform: translateX(100%);
    transition: transform-origin .3s ease-in-out;
    z-index: 100;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
  }

  @include big-mobile {
    flex-direction: column;
  }

  @include mobile {
    padding: 80px 25px 28px;
  }

}

.menu--active {
  transform: translateX(0);
  right: 16px;
  left: 16px;
  top: 20px;
  bottom: 20px;
}

.header__contacts--nav {
  display: none !important;

  @include big-mobile {
    display: flex !important;
    flex-direction: column;
  }

  & .header__mail {
    @include big-mobile {
      margin-bottom: 17px;
    }
  }

  & .header__tel {
    @include big-mobile {
      margin-bottom: 17px;
    }
  }
}
