.about {
  padding-top: 50px;

  @include big-desktop {
    padding-top: 78px;
  }

  @include tablet {
    padding-top: 45px;
  }

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 30px;

    @include tablet {
      font-size: 24px;
      line-height: 28px;
    }

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 30px;

    @include tablet {
      font-size: 16px;
      line-height: 30px;
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    margin-top: 90px;

    @include big-desktop {
      margin-top: 65px;
    }

    @include tablet {
      margin-top: 40px;
      justify-content: center;
      flex-wrap: wrap;
    }

    @include mobile {
      margin-top: 55px;
    }
  }

  &__item {
    max-width: 335px;
    display: flex;
    align-items: center;

    @include tablet {
      width: 100%;

    &:not(:last-child) {
      margin-bottom: 50px;
    }
    }
  }

  &__name {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: var(--primary-color);
    padding-left: 40px;
    margin: 0;

    @include tablet {
      font-size: 22px;
      line-height: 30px;
    }
  }

}
