/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1600px)) {
    @content;
  }
}

@mixin medium-desktop {
  @media (max-width: (1366px)) {
    @content;
  }
}

@mixin desktop {
  @media (max-width: (1260px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin big-mobile {
  @media (max-width: (640px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
