.hero {
  padding-top: 52px;
  padding-bottom: 105px;

  @include big-desktop {
    padding-top: 45px;
    padding-bottom: 90px;
  }

  @include mobile {
    padding-top: 20px;
    padding-bottom: 60px;
  }

  &__container {
    background-image: url(../img/hero-bg.png);
    background-size: cover;
    background-position: 0 0;
    background-repeat: no-repeat;
    position: relative;
    height: 560px;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;

    @include big-desktop {
      height: 581px;
    }

    @include desktop {
      margin-left: 25px;
      margin-right: 25px;
    }

    @include tablet {
      max-height: 351px;
    }

    @include mobile {
      margin-left: 15px;
      margin-right: 15px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #243469;
      opacity: 0.8;
      border-top-left-radius: 20px;
      border-bottom-right-radius: 20px;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 15px;
      right: 50px;
      max-width: 190px;
      max-height: 190px;
      width: 100%;
      height: 100%;
      background-color: #F03A2B;
      filter: blur(100px);

      @include big-mobile {
        z-index: -1;
      }
    }
  }

  &__wrapper {
    height: 100%;
    position: relative;
    background-clip: padding-box;
    border: solid 5px transparent;
    border-radius: 20px;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;

    &::before {
      content: "";
      position: absolute;
      top: -22px;
      right: 0;
      bottom: 23px;
      left: -38px;
      z-index: -3;
      margin: -5px;
      border-radius: inherit;
      background: linear-gradient(to right, #FCC009, #F03A2B);
      z-index: -10;

      @include big-mobile {
        display: none;
      }
    }

    &::after {
      content: "";
      position: absolute;
      top: -23px;
      right: 0;
      bottom: 22px;
      left: -39px;
      z-index: 2;
      border-radius: inherit;
      background: #fff;
      z-index: -10;

      @include big-mobile {
        display: none;
      }
    }
  }

  &__wrapper-second {
    height: 100%;
    position: relative;
    background-clip: padding-box;
    border: solid 5px transparent;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;

    &::before {
      content: "";
      position: absolute;
      top: 17px;
      right: -44px;
      bottom: -28px;
      left: 0;
      z-index: 0;
      margin: -5px;
      border-radius: inherit;
      background: linear-gradient(to right, #2D3A94, #51ABE3);
      z-index: -10;

      @include medium-desktop {
        left: 40px;
        top: 34px;
      }

      @include small-tablet {
        left: -10px;
        top: 15px;
      }

      @include big-mobile {
        display: none;
      }
    }

    &::after {
      content: "";
      position: absolute;
      top: 16px;
      right: -45px;
      bottom: -29px;
      left: 0;
      z-index: 0;
      border-radius: inherit;
      background: #fff;
      z-index: -10;

      @include medium-desktop {
        left: 40px;
        top: 34px;
      }

      @include small-tablet {
        left: -10px;
        top: 15px;
      }

      @include big-mobile {
        display: none;
      }
    }
  }

  &__title {
    font-weight: 700;
    font-size: 52px;
    line-height: 70px;
    text-transform: uppercase;
    color: var(--light-color);
    position: relative;
    top: 36%;
    max-width: 900px;
    left: 100px;
    z-index: 0;
    margin: 0;

    @include big-desktop {
      top: 33%;
    }

    @include tablet {
      font-size: 34px;
      line-height: 50px;
      left: 60px;
      max-width: 590px;
    }

    @include big-mobile {
      font-size: 26px;
      line-height: 36px;
      left: 19px;
      top: 122px;
      margin-left: -27px;
    }
  }

  &__btn {
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: var(--light-color);
    text-transform: lowercase;
    position: absolute;
    right: 16%;
    bottom: 87px;
    z-index: 0;

    @include big-desktop {
      right: 19%;
    }

    @include tablet {
      font-size: 22px;
      bottom: 34px;
      right: 155px;
    }

    @include mobile {
      font-size: 0;
      bottom: 24px;
      right: 7%;
    }

    &::before {
      content: '';
      position: absolute;
      top: -15px;
      right: -54%;
      width: 87px;
      height: 60px;
      background-image: linear-gradient(118.92deg, #F03A2B -8.89%, #FCC009 189.86%);
      box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.07);
      border-top-left-radius: 20px;
      border-bottom-right-radius: 20px;

      @include tablet {
        width: 65px;
        height: 45px;
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: -43%;
      width: 30px;
      height: 30px;
      background-image: url(../img/save.svg);
      background-size: cover;
      background-position: 0 0;
      background-repeat: no-repeat;

      @include tablet {
        width: 22px;
        height: 22px;
        top: -3px;
        right: -45%;
      }

      @include mobile {
        right: 20px;
      }
    }
  }
}
