.stepper {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 20px;

  &__wrapper {
    display: flex;
    width: 185px;
    height: 50px;
    user-select: none;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    border: 2px solid var(--title-color);
    width: 100%;
    height: 100%;
  }

  &__btn--down {
    border-top-left-radius: 20px;
  }

  &__btn--up {
    border-bottom-right-radius: 20px;
  }

  &__text {
    font-weight: 700;
    font-size: 18px;
    line-height: 34px;
    color: var(--primary-color);
  }

  &__field {
    overflow: hidden;
    display: inline-flex;
    align-items: center;
    border-left: 1px solid var(--title-color);
    border-right: 1px solid var(--title-color);
    border-top: 2px solid var(--title-color);
    border-bottom: 2px solid var(--title-color);
    width: 100%;
    height: 100%;
    padding-left: 20px;
  }

  &__input {
    border: none;

    height: 100%;
    outline: none;
    font-weight: 700;
    font-size: 18px;
    line-height: 34px;
  }

  &__btn--disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}
