.nav {
  display: flex;
  align-items: center;
  flex: 2 1 auto;
  margin-left: 56px;

  @include mobile {
    margin-left: 0;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include big-mobile {
      flex-direction: column;
      margin-bottom: 35px;
    }
  }

  &__item {
    max-width: 200px;

    @include big-desktop {
      max-width: 180px;
    }

    &:not(:last-child) {
      margin-right: 48px;

      @include big-desktop {
        margin-right: 40px;
      }

      @include big-mobile {
        margin-right: 0;

        &:not(:last-child) {
          margin-bottom: 25px;
        }
      }
    }
  }

  &__link {
    font-weight: 700;
    font-size: 15px;
    line-height: 17px;
    color: var(--light-color);

    @include big-mobile {
      font-weight: 700;
      font-size: 15px;
      line-height: 26px;
    }
  }
}
