.benefit {
  position: relative;
  padding-bottom: 120px;

  @include big-desktop {
    padding-bottom: 110px;
    padding-bottom: 90px;
  }

  @include mobile {
    padding-bottom: 66px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 36px;

    @include tablet {
      gap: 30px 23px;
    }

    @include mobile {
      gap: 25px 23px;
    }
  }

  &__item {
    background-color: var(--title-color);
    padding: 55px 45px 35px;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    grid-column: 4 span;
    min-height: 365px;
    position: relative;

    @include big-desktop {
      padding: 50px 27px 35px;
    }

    @include tablet {
      grid-column: 6 span;
    }

    @include big-mobile {
      grid-column: 12 span;
      padding: 50px 15px 35px;
    }

    @include mobile {
      &:nth-child(n+5) .benefit__image{
        left: 8px;
      }
    }
  }

  &__image {
    position: absolute;
    top: 35px;
    left: 45px;

    @include big-desktop {
      left: 23px;
    }

    @include tablet {
      width: 60px;
      height: 60px;
    }
  }

  &__item-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 35px;
    text-align: center;
    margin-left: 55px;

    @include big-desktop {
      font-size: 22px;
      line-height: 25px;
    }

    @include tablet {
      font-size: 20px;
      line-height: 23px;
    }
  }

  &__item-text {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: var(--light-color);
    margin: 0;

    @include tablet {
      font-size: 16px;
      line-height: 28px;
    }
  }
}
