.innovate {
  position: relative;
  padding-bottom: 94px;

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 55px;

    @include tablet {
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 50px;
    }

    @include mobile {
      margin-bottom: 30px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 55px 36px;
    margin-bottom: 50px;

    @include tablet {
      gap: 37px 20px;
    }

    @include mobile {

    }
  }

  &__item {
    grid-column: 4 span;

    @include tablet {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 12 span;
    }
  }

  &__item-title {
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    color: var(--primary-color);
    text-align: center;
    margin: 0;

    @include tablet {
      font-size: 22px;
      line-height: 25px;
    }
  }

  &__btns {
    text-align: center;
  }

  &__btn {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: var(--title-color);

    @include mobile {
      font-size: 18px;
      line-height: 28px;
    }
  }
}

.video {
  position: relative;
  width: 100%;
  height: 300px;
  overflow: hidden;
  margin-bottom: 20px;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;

  @include big-desktop {
    height: 245px;
  }

  @include mobile {
    height: 200px;
  }

  &--visible {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(42, 41, 40, 0.4);
      z-index: 1;
    }
  }

  & video {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 125px;
    height: 90px;
    transform: translate(-50%, -50%);
    color: var(--color-light);
    transition: color 0.3s ease-in-out;
    z-index: 2;

    @include tablet {
      width: 65px;
      height: 45px;
    }

    &:hover {
      opacity: 0.7;
    }

    &--hidden {
      display: none;
    }
  }
}
