.footer {
  background-color: var(--title-color);
  padding: 10px 0;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mobile {
      flex-direction: column;
    }
  }

  &__link {
    font-family: 'Acrom';
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: var(--light-color);
    transition: color 0.3s ease-in-out;

    // &:hover {
    //   color: var(--footerLink-color-hover);
    // }

    &-text {
      vertical-align: super;
    }
  }

  &__artix {
    fill: #FAFEFD;
    width: 20px;
    height: 21px;
    margin-left: 13px;
  }
}
