.decore {
  height: 100%;
  position: relative;
  border-top-left-radius: 20px;

  &::before {
    content: "";
    position: absolute;
    top: -22px;
    right: 0;
    bottom: 150px;
    left: -38px;
    z-index: -3;
    border: solid 5px;
    border-right-width: 0;
    margin: -5px;
    border-image: linear-gradient(to right, #FCC009, #F03A2B) 10;
    z-index: -1;
    max-width: calc((100% - 1878px) / 2 + 1700px);
    margin-left: auto;

    @include big-desktop {
      left: 59px;
    }

    @include big-mobile {
      display: none;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 25px;
    right: 0;
    bottom: 94px;
    left: 0;
    z-index: 0;
    border: solid 5px;
    border-left-width: 0;
    margin: -5px;
    border-image: linear-gradient(to right, #2D3A94, #51ABE3) 10;
    z-index: -1;
    max-width: calc((100% - 1890px) / 2 + 1700px);
    margin-right: auto;

    @include big-desktop {
      right: 57px;
    }

    @include medium-desktop {
      bottom: 73px;
    }

    @include big-mobile {
      display: none;
    }
  }
}

.decore--position {
  position: relative;

  &::before {
    content: "";
    top: 76px;
    bottom: 73px;
  }

  &::after {
    content: "";
    top: 132px;
    bottom: 46px;
  }
}
