.header {
  position: absolute;
  top: 65px;
  left: 0;
  width: 100%;
  z-index: 1;

  &__container {
    display: flex;
    justify-content: space-between;
    align-content: center;

    @include desktop {
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  &__contacts {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 2 1 auto;

    @include desktop {
      flex: 1 1 auto;
      justify-content: flex-end;
    }

    @include big-mobile {
      display: none;
      justify-content: flex-start;
      align-items: flex-start;
    }
  }

  &__mail {
    font-weight: 400;
    font-size: 15px;
    line-height: 17px;
    color: var(--light-color);

    @include desktop {
      margin-right: 50px;
    }

    @include tablet {
      margin-right: 32px;
    }
  }

  &__tel {
    font-weight: 700;
    font-size: 15px;
    line-height: 17px;
    text-transform: uppercase;
    color: var(--light-color);

    @include desktop {
      margin-right: 50px;
    }

    @include tablet {
      margin-right: 32px;
    }
  }

  &__social {
    display: flex;
    align-items: flex-start;
  }

  &__social-item {
    width: 19px;
    height: 19px;
    background-color: var(--light-color);
    border-radius: 50%;

    &:not(:last-child) {
      margin-right: 7px;
    }
  }

  &__social-link {
    display: block;
    transform: translate(50%, 0);

    &--tg {
      width: 9.5px;
      height: 8.5px;
    }

    &--wa {
      width: 10px;
      height: 10px;
    }

    &--vb {
      width: 10px;
      height: 11px;
    }
  }
}

.logo__image {
  @include big-desktop {
    width: 108px;
  }
}
