.compare {
  padding-bottom: 174px;

  @include tablet {
    padding-bottom: 157px;
  }

  @include mobile {
    padding-bottom: 33px;
  }

  &__container {
    background-image: url(../img/ogn.png);
    background-size: 597px 698px;
    background-position: center 95px;
    background-repeat: no-repeat;

    @include big-desktop {
      background-size: 460px 539px;
      background-position: center 130px;
    }

    @include tablet {
      background-size: 272px 320px;
      background-position: center 155px;
    }

    @include big-mobile {
      position: relative;
      z-index: 0;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #fff;
        opacity: 0.8;
      }
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    position: relative;
    padding-bottom: 35px;

    @include big-mobile {
      z-index: 1;
    }
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 39px;

    @include big-desktop {
      margin-bottom: 54px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 28px;
    }

    @include big-mobile {
      position: relative;
      z-index: 1;
    }
  }

  &__left {

    @include big-mobile {
      width: 100%;
      position: relative;
    }

    & .compare__item {

      &::before {
        content: '';
        position: absolute;
        bottom: -22px;
        left: 0;
        width: 150%;
        height: 3px;
        background: linear-gradient(90deg, #2D3A94, #51ABE3);
        z-index: -1;
        opacity: 0.5;

        @include tablet {
          width: 106%;
          bottom: -8px;
        }

        @include big-mobile {
          width: 320%;
        }
      }

      &::after {
        content: '';

        @include big-mobile {
          display: none;
        }
      }

      @include big-desktop {
        &:nth-child(n+5) {
          &::after {
            content: '';
            position: absolute;
            bottom: 43px;
            right: -100%;
            width: 60%;
            height: 3px;
            background: linear-gradient(90deg, #51ABE3, #2D3A94);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(-45deg);
          }
        }
      }

      @include tablet {

        &:nth-child(n+3):nth-child(-n+5) {
          &::before {
            width: 260%;
          }
        }

        &:nth-child(2) {
          &::after {
            content: '';
            position: absolute;
            bottom: -34px;
            right: -104%;
            width: 100%;
            height: 3px;
            background: linear-gradient(90deg, #51ABE3, #2D3A94);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(16deg);
          }
        }

        &:nth-child(5) {
          &::after {
            content: '';
            display: none;
          }
        }

        &:nth-child(6) {
          &::after {
            content: '';
            position: absolute;
            bottom: 23px;
            right: -123%;
            width: 120%;
            height: 3px;
            background: linear-gradient(90deg, #51ABE3, #2D3A94);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(-16deg);
          }
        }

        &:nth-child(7) {
          &::after {
            content: '';
            position: absolute;
            bottom: 39px;
            right: -120%;
            width: 120%;
            height: 3px;
            background: linear-gradient(90deg, #51ABE3, #2D3A94);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(-25deg);
          }
        }

        &:nth-child(8) {
          &::after {
            content: '';
            position: absolute;
            bottom: 62px;
            right: -121%;
            width: 127%;
            height: 3px;
            background: linear-gradient(90deg, #51ABE3, #2D3A94);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(-36deg);
          }
        }

        &:nth-child(9) {
          &::after {
            content: '';
            position: absolute;
            bottom: 86px;
            right: -133%;
            width: 147%;
            height: 3px;
            background: linear-gradient(90deg, #51ABE3, #2D3A94);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(-43deg);
          }
        }
      }

      @include big-mobile {

        padding-top: 20px;

        &:nth-child(n+3):nth-child(-n+5) {
          &::before {
            width: 320%;
          }
        }
      }
    }
  }

  &__list {}

  &__list-title {
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    text-transform: uppercase;
    color: var(--title-color);
    margin: 0;
    margin-bottom: 42px;

    @include tablet {
      margin-bottom: 5px;
    }
  }

  &__item {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: var(--primary-color);
    position: relative;

    @include big-desktop {
      font-size: 16px;
      line-height: 18px;
      display: flex;
      align-items: center;
    }

    @include tablet {
      flex-direction: column;
      align-items: flex-start;
    }

    &:not(:last-child) {
      margin-bottom: 52px;

      @include big-desktop {
        margin-bottom: 37px;
      }

      @include tablet {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 15px;
      }
    }
  }

  &__item--bold {
    font-weight: 700;
    font-size: 24px;
    padding-left: 20px;

    @include big-desktop {
      font-size: 22px;
    }

    @include tablet {
      padding-left: 0;
      font-size: 20px;
    }
  }

  &__item-name {
    @include big-desktop {
      max-width: 160px;
    }

    @include tablet {
      max-width: 100%;
      margin-bottom: 5px;
      font-size: 12px;
      line-height: 21px;
    }

    @include big-mobile {
      position: absolute;
      left: 70px;
      top: 0;
      text-align: center;
      width: 100%;
    }
  }

  &__right {
    text-align: right;

    & .compare__item {

      @include big-desktop {
        justify-content: flex-end;
      }

      @include tablet {
        align-items: flex-end;

        &:nth-child(n+3):nth-child(-n+5) {
          &::before {
            width: 260%;
          }
        }
      }

      @include big-mobile {
        padding-top: 20px;
      }

      @include big-mobile {
        & .compare__item-name {
          font-size: 0;
          line-height: 16;
        }
      }

      &::before {
        content: '';
        position: absolute;
        bottom: -22px;
        right: 0;
        width: 150%;
        height: 3px;
        background: linear-gradient(90deg, #FCC009, #F14624);
        z-index: -1;
        opacity: 0.5;

        @include tablet {
          width: 106%;
          bottom: -8px;
        }

        @include big-mobile {
          display: none;
        }
      }

      &::after {
        content: '';

        @include big-mobile {
          display: none;
        }
      }

      @include big-desktop {
        &:nth-child(n+5) {
          &::after {
            content: '';
            position: absolute;
            bottom: 91px;
            left: -434px;
            width: 100%;
            height: 3px;
            background: linear-gradient(90deg, #F14624, #FCC009);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(45deg);
          }
        }
      }

      @include tablet {
        &:nth-child(2) {
          &::after {
            content: '';
            position: absolute;
            bottom: -35px;
            left: -101%;
            width: 100%;
            height: 3px;
            background: linear-gradient(90deg, #FCC009, #F14624);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(163deg);
          }
        }

        &:nth-child(5) {
          &::after {
            content: '';
            display: none;
          }
        }

        &:nth-child(6) {
          &::after {
            content: '';
            position: absolute;
            bottom: 6px;
            left: -104%;
            width: 100%;
            height: 3px;
            background: linear-gradient(90deg, #FCC009, #F14624);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(-171deg);
          }
        }

        &:nth-child(7) {
          &::after {
            content: '';
            position: absolute;
            bottom: 33px;
            left: -108%;
            width: 108%;
            height: 3px;
            background: linear-gradient(90deg, #FCC009, #F14624);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(-156deg);
          }
        }

        &:nth-child(8) {
          &::after {
            content: '';
            position: absolute;
            bottom: 59px;
            left: -121%;
            width: 128%;
            height: 3px;
            background: linear-gradient(90deg, #FCC009, #F14624);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(-146deg);
          }
        }

        &:nth-child(9) {
          &::after {
            content: '';
            position: absolute;
            bottom: 83px;
            left: -128%;
            width: 142%;
            height: 3px;
            background: linear-gradient(90deg, #FCC009, #F14624);
            z-index: -1;
            opacity: 0.5;
            transform: rotate(-137deg);
          }
        }
      }
    }
  }
}
