.contacts {

  &__container {
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    background-color: var(--title-color);
    padding: 40px 60px 53px;
    position: relative;
    z-index: 1;

    @include big-desktop {
      padding: 34px 48px 53px;
    }

    @include tablet {
      padding: 34px 48px 25px;
    }

    @include big-mobile {
      padding: 24px 20px 20px;
      margin-left: 15px;
      margin-right: 15px;
    }

    &::before {
      content: '';
      position: absolute;
      top: -90px;
      right: -24px;
      width: 410px;
      height: 450px;
      background-image: url(../img/cont-logo.svg);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: 0 0;

      @include big-desktop {
        top: -74px;
        width: 358px;
        height: 395px;
      }

      @include tablet {
        width: 318px;
        height: 351px;
        top: -35px;
        right: 26px;
      }

      @include mobile {
        width: 250px;
        height: 275px;
        top: 155px;
        right: -85px;
      }
    }
  }

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 25px;

    @include tablet {
      font-weight: 700;
      font-size: 24px;
      margin-bottom: 0;
      position: relative;
      z-index: 1;
    }

    @include big-mobile {
      margin-bottom: 15px;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: flex-start;

    @include big-mobile {
      flex-direction: column;
    }
  }

  &__col {
    max-width: 260px;
    width: 100%;
    position: relative;

    @include tablet {
      max-width: 190px;
    }

    @include big-mobile {
      max-width: 100%;
      margin-bottom: 30px;
    }
  }

  &__col-wrapper {
    display: flex;
    justify-content: space-between;
    margin-left: 190px;
    max-width: 625px;
    width: 100%;

    @include big-desktop {
      margin-left: 60px;
      max-width: 505px;
    }

    @include tablet {
      flex-direction: column;
      margin-left: 45px;
    }

    @include big-mobile {
      margin-left: 0;
    }
  }

  &__col-title {
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 5px;

    @include tablet {
      font-size: 16px;
      line-height: 28px;
    }
  }

  &__col-text {
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    color: var(--light-color);
    font-style: normal;

    @include tablet {
      font-size: 16px;
      line-height: 28px;
    }
  }

  &__social {
    display: flex;
    align-items: flex-start;
    margin-top: 12px;
  }

  &__social-item {
    width: 30px;
    height: 30px;
    background-image: linear-gradient(118.92deg, #F03A2B -8.89%, #FCC009 189.86%);
    border-radius: 50%;

    @include tablet {
      width: 20px;
      height: 20px;
    }

    &:not(:last-child) {
      margin-right: 12px;
    }
  }

  &__social-link {
    display: block;
    transform: translate(50%, 50%);

    @include tablet {
      transform: translate(42%, 15%);
    }

    &--tg {
      width: 15px;
      height: 13px;

      @include tablet {
        width: 10px;
        height: 9px;
      }
    }

    &--wa {
      width: 16px;
      height: 16px;

      @include tablet {
        width: 10.5px;
        height: 10.5px;
      }
    }

    &--vb {
      width: 16px;
      height: 17px;

      @include tablet {
        width: 10.5px;
        height: 11.5px;
      }
    }
  }
}
